.scrcpy-container {
  max-width: 1200px;
  margin: 0 auto;
}

.scrcpy-container .video-section {
  position: relative;
}

.scrcpy-container .video-section .video-container {
  background-color: #f0f0f0;
  border: 1px solid #e0e0e0;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 300px;
  display: flex;
  overflow: hidden;
}

.scrcpy-container .video-section .video-container canvas {
  margin: 0 auto;
  display: block;
}

.scrcpy-container .video-section .video-container:empty:after {
  content: "Connect a device to display the screen";
  color: #999;
  font-size: 14px;
}

.scrcpy-container .video-section .screen-info {
  color: #fff;
  background-color: #00000080;
  border-radius: 4px;
  padding: 2px 8px;
  font-size: 12px;
  position: absolute;
  bottom: 8px;
  right: 8px;
}

.scrcpy-container .controls-section {
  flex-wrap: wrap;
  gap: 16px;
  margin-top: 16px;
  display: flex;
}

.scrcpy-container .controls-section .button-group {
  gap: 8px;
  margin-right: 16px;
  display: flex;
}

@media (max-width: 768px) {
  .scrcpy-container .controls-section .button-group {
    margin-bottom: 8px;
    margin-right: 0;
  }

  .scrcpy-container .controls-section {
    flex-direction: column;
    align-items: flex-start;
  }
}

@media (max-width: 576px) {
  .scrcpy-container .controls-section {
    flex-direction: column;
  }

  .scrcpy-container .controls-section .ant-space {
    margin-bottom: 8px;
  }
}
